window.dom ={
    create(node){
        let temp = document.createElement('template')
        temp.innerHTML=node.trim()
        return temp.content.firstChild   //ËøîÂõûËé∑ÂæóÁöÑÂÖÉÁ¥†
    },
    before(node1,node2){
        node1.parentNode.insertBefore(node2,node1.nextSibling)//ËøôÈáåÊòØÊäänode2ÊèíÂÖ•Âà∞node1ÂâçÈù¢
    },
    after(node1,node2){
        node2.parentNode.insertBefore(node1,node2)//ËøôÈáåÊòØÊäänode2ÊèíÂà∞node1ÁöÑÂêéÈù¢
    },
    append(parentNode, node){
        parentNode.append(node)
    },
    wrap(node, parentNode){ //ÊèíÂÖ•‰∏Ä‰∏™Êñ∞ÁöÑÁà∂ÂÖÉÁ¥†
        dom.before(node,parentNode)
        dom.append(parentNode,node)
    },
    remove(node){
        node.parentNode.removeChild(node)
        return node   //Â∞ÜÁßªÈô§ÁöÑnodeËäÇÁÇπËøõË°åÂà†Èô§
    },
    // empty(node){
    //     let {childNodes} = node //Ëé∑ÂèñÊâÄÊúâÁöÑÂ≠êËäÇÁÇπ
    //     console.log(childNodes);
    //     let emptyArray = []
    //     for(let item of childNodes){
    //         emptyArray.push(item)
    //     }
    //     node.innerHTML='' //Ê∏ÖÁ©∫node
    //     return emptyArray;
    // },
    empty(node){//‰ª•‰∏äÊñπÊ≥ïÂ•ΩÂÉèÊúâÁÇπÈóÆÈ¢òÔºüÔºü,‰ΩÜÊòØÊàëÊÑüËßâÂÖ∂ÂÆûÊòØÂèØ‰ª•ËøõË°å‰ΩøÁî®ÁöÑÔºåËøôÈáåÁªïËøá‰∫ÜÂØπlengthÁöÑËé∑Âèñ
        let emptyArray = []
        let first = node.firstChild
        while(first){//Âä®ÊÄÅËøõË°åpushÔºåÊØèÊ¨°Â∞Ü‰πãÂâçÁöÑnodeËøõË°åËøêÁÆó
            emptyArray.push(dom.remove(first))//Ë∞ÉÁî®‰πãÂâçÁöÑÁÆóÊ≥ïÔºåÂ∞ÜÂâçÈù¢ÁöÑ‰∏úË•øËøõË°åpush
            // node.removeChild(node.firstChild)
             first = node.firstChild
        }
        return emptyArray
    },
    attr(node, name, value){ //ËØªÂÜôdomËäÇÁÇπÁöÑÂ±ûÊÄß
        if(arguments.length===3){
            node.setAttribute(name,value)
        }else if(arguments.length ===2){
            return node.getAttribute(name)
        }  
    },
    text(node,string){
        if(arguments.length ===2){
            if('innerText' in node){ //ÈÄÇÈÖçie
            node.innerText = string
            }else{
                node.textContent = string
            }
        }else{
            if('innerText' in node){ //ÈÄÇÈÖçie
                return node.innerText
            }else{
            return node.textContent
            }
        }
    }
}
